<template>
  <BaseModal v-model="modalIsActive" :z-index="1056" scrollable persist>
    <template #header
      ><span :style="{ color: Color.SoftBlue }"> Numbers List </span>
    </template>
    <template #body>
      <div :style="{ color: Color.SoftBlue }" class="px-4 pb-4">
        <ul class="styled-list">
          <li v-for="(number, index) in props.numbers" :key="index">
            <a
              href="javascript:"
              :style="{ color: Color.SoftBlue }"
              @click="call(number)"
            >
              <span><BaseIcon :size="16" name="phone" /></span>
              {{ phoneMasked(number) }}
            </a>
          </li>
        </ul>
      </div>
    </template>
    <template #footer>
      <div class="d-flex gap-3">
        <button
          class="secondary-btn text-uppercase"
          @click="modalIsActive = false"
        >
          {{ t('Cancel') }}
        </button>
      </div>
    </template>
  </BaseModal>
</template>

<script setup lang="ts">
import BaseModal from '@/components/BaseModal.vue';
import { ref, watch } from 'vue';
import { Color } from '@/enums';
import { useUserAgent } from '@/composables';
import BaseIcon from '@/components/BaseIcon.vue';
import { useI18n } from 'vue-i18n';
import { phoneMasked } from '@/utils';
const { t } = useI18n();

const { newCall } = useUserAgent();

const emit = defineEmits<{
  (e: 'update:modelValue', value: boolean): void;
}>();

function call(number: string) {
  newCall(number);
  modalIsActive.value = false;
}

const props = defineProps<{
  modelValue: boolean;
  numbers: string[];
}>();

const modalIsActive = ref(props.modelValue);

watch(modalIsActive, (value) => {
  emit('update:modelValue', value);
});

watch(
  () => props.modelValue,
  (value) => {
    modalIsActive.value = value;
  }
);
</script>
<style>
.styled-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.styled-list li a {
  text-decoration: none;
  line-height: 35px;
}
</style>
